import React from 'react';

const FormInput = ({tag,type,name,placeholder,classes, value,required, onChange,onClick}) => {
    return (
        <div className="single-input-item">
            <label>
                {(()=>{
                    if(tag === 'input'){
                        return <input type={type} name={name} placeholder={placeholder} className={classes} value={value} required={required} onChange={e => onChange(e.target.value)}/>
                    }else if(tag === 'textarea'){
                        return  <textarea name={name} cols="30" rows="7" placeholder={placeholder} className={classes} required={required} value={value} onChange={e => onChange(e.target.value)}/>
                    }else if(tag === 'button'){
                        return <button className={`btn-outline ${classes}`} onClick={onClick} type={type ?? "button"}>Send Message</button>
                    }
                })()}
            </label>
        </div>
    );
};

export default FormInput;